@font-face {
  font-family: 'OpenSans';
  src: local("OpenSans Bold"), local("OpenSans-Bold"), url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff"), url("../fonts/OpenSans-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'OpenSans';
  src: local("OpenSans Regular"), local("OpenSans-Regular"), url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff"), url("../fonts/OpenSans-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Mansalva';
  src: local("Mansalva Regular"), local("Mansalva-Regular"), url("../fonts/Mansalva-Regular.woff2") format("woff2"), url("../fonts/Mansalva-Regular.woff") format("woff"), url("../fonts/Mansalva-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  background-color: #E0FFFF;
  font-family: "OpenSans", sans-serif;
}

* {
  box-sizing: border-box;
}

.wrapper {
  width: calc(100% - 20px);
  margin: 0 auto;
}

@media (min-width: 960px) {
  .wrapper {
    width: calc(100% - 40px);
  }
}

@media (min-width: 1200px) {
  .wrapper {
    width: calc(100% - 80px);
  }
}

.page-header {
  height: 100px;
  width: 100%;
  font-family: 'Mansalva', sans-serif;
  text-transform: uppercase;
  font-size: 40px;
}

.page-header__logo {
  margin-bottom: 0;
  margin-top: 10px;
}

.cards-list {
  display: grid;
  grid-template-columns: 100%;
}

@media (min-width: 1024px) {
  .cards-list {
    grid-template-columns: 48% 48%;
    grid-column-gap: 20px;
    -ms-grid-column-gap: 20px;
  }
}

@media (min-width: 1200px) {
  .cards-list {
    grid-template-columns: 32% 32% 32%;
  }
}

.main-card {
  width: 100%;
  border: 3px solid #000;
  margin-bottom: 10px;
}

@media (min-width: 960px) {
  .main-card {
    margin-bottom: 30px;
  }
}

.main-card ul {
  padding-left: 20px;
}

.main-card p {
  margin: 0;
}

.main-card__header {
  height: 60px;
  line-height: 60px;
  text-align: center;
  background-color: #98FB98;
}

@media (min-width: 1024px) {
  .main-card__header {
    border-bottom: 3px solid #000;
  }
}

.main-card__header.active {
  border-bottom: 3px solid #000;
}

.main-card__header.active + .main-card__body {
  transition: all 0.4s;
  display: block;
}

.main-card__title {
  font-size: 30px;
  margin: 0;
  font-family: 'Mansalva', sans-serif;
}

.main-card__body {
  padding: 20px;
  display: none;
  line-height: 30px;
}

@media (min-width: 1024px) {
  .main-card__body {
    display: block;
  }
}

.main-card__body p,
.main-card__body ul li {
  word-break: break-word;
}

.main-card__text {
  font-size: 20px;
  margin: 0;
}

@media (min-width: 1200px) {
  .main-card__text {
    font-size: 16px;
  }
}

.search {
  width: 100%;
  margin-bottom: 50px;
}

.search input {
  width: 100%;
  outline: none;
  height: 30px;
  line-height: 30px;
  font-size: 20px;
  border: none;
  border-bottom: 2px solid #000;
  background-color: transparent;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 5px;
}

@media (min-width: 1024px) {
  .search input {
    width: 500px;
  }
}

.search input::placeholder {
  color: #000;
  opacity: 0.7;
}
